.main {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  padding-top: 64px;

  &.menu-open {
    position: fixed;
    overflow: hidden;
  }


  &__body {
    flex: 1;
  }
}

img.layout {
  position: absolute;
  top: 0px;
  bottom: 0;
  opacity: 0.2;
  filter: invert(1);
  align-self: center;
}