.mainHeader {
  width: 100%;
  padding-top: 7px;
  padding-bottom: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  background-color: #ffffff;
  z-index: 10000;

  &__container {
    width: 1140px;
    display: flex;
    justify-content: space-between;
  }

  &__logo {
    margin-top: 13px;

    &-item {
      width: 134px;
      height: 24px;
      cursor: pointer;
      transition: 0.2s fill ease-in-out;

      &:hover {
        //fill: #ef6c00;
      }
    }
  }

  &__menu {
    margin-top: 16px;

    &-mobile {
      width: 259px;
      //min-height: 100vh;
      //background-color: red;
      //position: absolute;
      //top: 0;
      //left: 0;
      //transition: 0.2s transform ease-in-out;

      &-toggle {
        display: none;
        margin-right: -12px;

        &::selection {
          text-shadow: none;
        }
      }

      &-bg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba(#160c0d, 0.32);

        &.disabled {
          display: none;
        }
      }
    }
  }

  &__sidebar-mobile {
    width: 259px;
    display: flex;
    flex-direction: column;
    //align-items: center;
    padding-top: 30px;

    &-current-user {
      padding-left: 24px;
    }

    &-menu {
      padding-left: 24px;
    }

    &-hr {
      border-bottom: 1px solid #e1e1e1;
      width: 100%;
      margin-top: 25px;
      margin-bottom: 32px;
    }
  }

  &__current-user {
    margin-top: 1px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 246px;
    margin-left: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    //margin-left: 60px;
  }

  &__sign-out {
    margin-right: -12px;
    margin-left: 3px;
  }

  &__navigation {
    display: flex;
  }
}

@media (max-width: 1170px) {
  .mainHeader {

    &__container {
      width: 929px;
    }

    &__menu {
      margin-top: 15px;
    }
  }
}

@media (max-width: 960px) {
  .mainHeader {
    padding-bottom: 9px;

    &__menu {
      display: none;

      &-mobile {

        &-toggle {
          display: block;
        }
      }
    }

    &__current-user {
      display: none;
    }

    &__container {
      width: 721px;
    }
  }
}

@media (max-width: 755px) {
  .mainHeader {

    &__container {
      width: 100vw;
      padding: 0 15px;
      box-sizing: border-box;
    }

    &__sidebar-mobile {
      width: 250px;

      &-current-user {
        padding-left: 15px;
      }

      &-menu {
        padding-left: 15px;
      }
    }
  }
}