.menu {

  &__items {
    display: flex;

    &.mobile {
      flex-direction: column;
    }
  }

  &__item {
    font-family: "Source Sans Pro", sans-serif;
    font-size: 16px;
    margin-right: 29px;
    cursor: pointer;

    span {
      color: #000000;
      transition: 0.2s color ease-in-out;

      &:hover {
        color: #ef6c00;
      }
    }

    &:last-child {
      margin-right: 0;
    }

    &.mobile {
      margin-right: 0;
      margin-top: 27px;

      &:first-child {
        margin-top: 0;
      }
    }

    &.footer {
      span {
        color: #ffffff;

        &:hover {
          color: #ef6c00;
        }
      }
    }
  }
}

@media (max-width: 721px) {
  .menu {

    &__items.footer {
      flex-direction: column;
      align-items: center;
      margin-top: 3px;
    }

    &__item.footer {
      margin-right: 0;
      margin-top: 10px;

      &:first-child {
        margin-top: 0;
      }
    }
  }
}